import './styles';

import { animateHero, prefetchImage, initializeLazyElements, hideSpin, handlePortfolioCta, animateHeader } from './utils';

const prefetchImages = window.__PREFETCH_IMAGES__ || [];
const isIndex = !!window.__IS_INDEX__;

document.addEventListener('DOMContentLoaded', () => {
  initializeLazyElements();

  const initialize = () => {
    return hideSpin()
      .then(() => {
        if (isIndex) {
          animateHero();
          handlePortfolioCta();
        } else {
          animateHeader();
        }
      });
  };

  if (prefetchImages.length > 0) {
    Promise.all(prefetchImages.map(prefetchImage)).then(initialize);
  } else {
    initialize();
  }
});
