import anime from 'animejs';
import inView from 'inview';
import scroll from 'scroll';
import scrollDoc from 'scroll-doc';
import ease from 'ease-component';

export const prefetchImage = url => {
  const image = document.createElement('img');

  const promise = new Promise((resolve, reject) => {
    image.addEventListener('load', resolve);
  });

  image.src = url;

  return promise;
};

export const handlePortfolioCta = () => {
  const cta = document.getElementById('portfolioCta');
  const portfolio = document.getElementById('portfolioSection');
  const page = scrollDoc();

  cta.addEventListener('click', (e) => {
    scroll.top(page, portfolio.offsetTop, { easing: ease.outQuad });
  });
}

export const initializeLazyElements = () => {
  const lazyElements = Array.from(
    document.querySelectorAll('[data-lazy-background-image], [data-lazy-src]')
  );

  lazyElements.forEach(element => {
    inView(element, function(isInview) {
      if (isInview) {
        const src = element.getAttribute('data-lazy-src');
        const backgroundImage = element.getAttribute('data-lazy-background-image');
        const url = src || backgroundImage;

        prefetchImage(url)
          .then(() => {
            if (src) {
              element.src = url;
            } else {
              element.style.backgroundImage = `url(${url})`;
            }

            element.classList.add('lazy--loaded');
            this.destroy();
          });
      }
    });
  });
};

export const hideSpin = () => {
  const spin = document.querySelector('.spin');

  anime({
    targets: '.spin .spin__content',
    scale: [1, 0],
    duration: 1000,
  });

  return anime({
    targets: '.spin',
    opacity: [1, 0],
    duration: 1000,
  })
    .finished
    .then(() => {
      spin.style.display = 'none';
    });
}

export const animateHeader = () => {
  const timeline = anime.timeline();

  timeline
    .add({
      targets: '.header .header__title',
      translateY: [-20, 0],
      opacity: [0, 1],
      easing: 'easeOutExpo',
      duration: 1000,
    })
    .add({
      targets: '.header .header__role',
      translateY: [20, 0],
      opacity: [0, 1],
      easing: 'easeOutExpo',
      duration: 1000,
      offset: '-=500',
    });
}

export const animateHero = () => {
  let timeline = anime.timeline();

  timeline = timeline
    .add({
      targets: '.hero .hero__image',
      scale: [0, 1],
      opacity: [0, 1],
      easing: 'easeOutExpo',
      duration: 1000,
    })
    .add({
      targets: '.hero .hero__helloText',
      translateY: [-20, 0],
      opacity: [0, 1],
      easing: 'easeOutExpo',
      duration: 1000,
      offset: '-=500',
    })
    .add({
      targets: '.hero .hero__elinaText',
      translateY: [20, 0],
      opacity: [0, 1],
      easing: 'easeOutExpo',
      duration: 1000,
      offset: '-=500',
    });

  timeline = ['.hero__email', '.hero__twitter', '.hero__linkedin', '.hero__youtube']
    .reduce((acc, curr) => {
      return acc
        .add({
          targets: `.hero ${curr}`,
          translateX: [-100, 0],
          opacity: [0, 1],
          rotate: [-180, 0],
          easing: 'easeOutExpo',
          duration: 1000,
          offset: '-=800',
        });
    }, timeline);

  timeline
    .add({
      targets: '.hero .hero__cta .button',
      scale: [0, 1],
      opacity: [0, 1],
      easing: 'easeOutExpo',
      duration: 1000,
      offset: '-=500',
    });
};
